import BrowserHandler from '../common/browserHandler';

if (!(window as any).Utilities) {
    (window as any).Utilities = {
        // Deserialize query string to an object
        deserializeQuery: function (query: string): any {
            var match: RegExpExecArray,
                pl = /\+/g, // Regex for replacing addition symbol with a space
                search = /([^&=]+)=?([^&]*)/g,
                decode = function (s: string): string {
                    return decodeURIComponent(s.replace(pl, ' '));
                },
                obj: any = {};
            match = search.exec(query);
            while (match) {
                obj[decode(match[1])] = decode(match[2]);
                match = search.exec(query);
            }

            return obj;
        },

        // Gets the file extension
        getFileExtension: function (fileName: string): string {
            var docExt = '';
            if (fileName) {
                var splitDoc = fileName.match(/(?:\.)(\w+)$/);
                if (splitDoc && splitDoc.length > 1) {
                    docExt = splitDoc[splitDoc.length - 1].toLowerCase();
                }
            }

            return docExt;
        },

        // Navigate to url
        navigateToUrlWithNewTab: function (url: string, openActive: boolean): void {
            BrowserHandler.tabs.create({
                url: url,
                active: this.isUndefinedOrNull(openActive) ? true : openActive,
            });
            var props: chrome.tabs.CreateProperties;
        },

        // Simple check for not undefined or null
        isNotUndefinedOrNull: function (obj: any): boolean {
            return !this.isUndefinedOrNull(obj);
        },

        // Simple check for undefined or null
        isUndefinedOrNull: function (obj: any): boolean {
            return typeof obj === 'undefined' || obj === null;
        },

        // Determines whether the extension is running in testing mode (running jasmine unit tests)
        isExtensionInTestingMode: function (): boolean {
            return this.isNotUndefinedOrNull((window as any).jasmine);
        },

        // Determines whether extension is running in developer mode; defaults to true if inconclusive
        isExtensionInDevelopmentMode: function (): boolean {
            // testing mode is not development mode
            if (this.isExtensionInTestingMode()) {
                return false;
            }

            if ((window as any).browser && (window as any).browser.extension) {
                var url = BrowserHandler.runtime.getURL('');

                if (typeof url !== 'undefined' && url !== null) {
                    if (/_microsoftofficeonline_/i.test(url)) {
                        return false; // from store
                    }
                }
            }
            if ((window as any).chrome && (window as any).chrome.extension) {
                // Make sure chrome manifest exists
                var manifest = this.getManifest();
                if (manifest === undefined || manifest === null) {
                    return false;
                }

                // Google Web Store adds key and update_url keys to the manifest
                if (manifest.key !== undefined || manifest.update_url !== undefined) {
                    return false;
                }
            }
            return true;
        },

        // Determines whether Editor is enabled by looking at the manifest.
        // This should work only in chrome.
        // TODO: change this to something more generic like send a message that only the desired background
        // script will respond to, or generate a look up of partner extensions using their name as found in
        // their manifest "name" which is updated when the package is updated/added
        isEditorEnabled: function (): boolean {
            if (!(window.chrome && window.chrome.extension)) {
                return false;
            }

            // Make sure chrome manifest exists
            var manifest = this.getManifest();

            // Editor adds a specific background page to the extension
            return manifest && manifest.background && manifest.background.scripts && manifest.background.scripts.includes('editor/background.js');
        },

        // Determines whether Editor marketing is enabled by looking at the manifest.
        // This should work only in chrome.
        shouldShowEditorMaterial: function (): boolean {
            if (!(window.chrome && window.chrome.extension)) {
                return false;
            }

            // Make sure chrome manifest exists
            var manifest = this.getManifest();

            // Editor adds a specific background page to the extension
            return manifest && manifest.version_name && manifest.version_name.indexOf('[ed]') != -1;
        },

        // Returns the message id to use.
        // This should work only in chrome.
        getAppDescription: function (): string {
            if (!(window.chrome && window.chrome.extension)) {
                return undefined;
            }

            // Make sure chrome manifest exists
            var manifest = this.getManifest();

            return manifest.description;
        },

        // Gets the Chrome runtime manifest object
        getManifest: function (): any {
            // Make sure the BrowserHandler.runtime.getManifest() function exists
            if (typeof BrowserHandler.runtime !== 'object' || typeof BrowserHandler.runtime.getManifest !== 'function') {
                return null;
            }

            // Make sure manifest exists
            var manifest = BrowserHandler.runtime.getManifest();
            if (manifest === undefined || manifest === null) {
                return null;
            }

            return manifest;
        },

        // Returns whether the current context is running in the extension background
        isBackgroundContext: function (): boolean {
            var loc = window.location.href;

            if ((window as any).browser && (window as any).browser.extension) {
                if (/^ms-browser-extension/.test(loc) && window === BrowserHandler.extension.getBackgroundPage()) {
                    return true;
                }
            }

            // Modern chrome does define window.browser so we need to re-check
            if (window.chrome && window.chrome.extension) {
                if (/^chrome/.test(loc) && window === BrowserHandler.extension.getBackgroundPage()) {
                    return true;
                }
            }

            return false;
        },

        // Gets the current time
        getCurrentTime: function (): number {
            return Math.round(new Date().getTime() / 1000.0);
        },

        // Returns true if we are running on Chrome, false otherwise
        isChrome: function (): boolean {
            // Ref: http://stackoverflow.com/questions/4565112/javascript-how-to-find-out-if-the-user-browser-is-chrome/13348618
            var nav = window.navigator;
            var isOpera = nav.userAgent.indexOf('OPR') > -1;
            // Anaheim has just Edg
            var isIEedge = nav.userAgent.indexOf('Edg') > -1;

            return this.isNotUndefinedOrNull((window as any).chrome) && nav.vendor.indexOf('Google') > -1 && isOpera === false && isIEedge === false;
        },

        // Returns the name of the current browser (Chrome or Edge)
        getBrowserName: function (): string {
            if (this.isChrome()) {
                return 'Chrome';
            }

            return 'Edge';
        },

        // Following code is required since Edge doesn't support "__MSG_@@bidi_dir__" in RS1
        isRTL: function (): boolean {
            var uiLang = BrowserHandler.i18n.getUILanguage();
            return /^ar/i.test(uiLang) || /^fa/i.test(uiLang) || /^he/i.test(uiLang) ? true : false;
        },

        // Set the pop up icon
        setIcon: function (iconPath: any): void {
            BrowserHandler.browserAction.setIcon({
                path: iconPath,
            });
        },
    };
}

export default (window as any).Utilities;
